<style type="text/css">
  * {
    margin: 0;
    padding: 0;
    list-style: none;
  }
</style>
<template>
  <div style="text-align: left;">
    <div style="text-align: right;background: #545c64;min-height: 36px;">
      <headerMenu ref="headerMenu"></headerMenu>
    </div>
    <div id="hollowCircle" style="width: 500px;height: 500px;display: inline-block;"></div>
    <div id="cake" style="width: 500px;height: 500px;border:1px solid rgb(180,180,180);display: inline-block;"></div>
    <div></div>
    <div id="column" style="width: 500px;height: 300px;display: inline-block;"></div>
    <div id="lines" style="width: 500px;height: 300px;display: inline-block;"></div>
    <div id="line" style="width: 500px;height: 300px;display: inline-block;"></div>
    <div></div>
    <div id="chartMap" style="width: 100%;height: 800px;"></div>
  </div>
</template>
<script type="text/javascript">
  import headerMenu from '@/views/common/header-menu.vue'
  import china from '../../../static/js/echarts/china.js'

  export default {
    name: 'echarts',
    components: {
      'headerMenu': headerMenu
    },
    data() {
      return {}
    },
    created() {

    },
    mounted() {
      this.$refs.headerMenu.setMenuIndex("6")
      this.$nextTick(function () {
        this.drawHollowCircle()
        this.drawColumn()
        this.drawCake()
        this.drawLines()
        this.drawLine()
        this.drawChartMap()
      })
    },
    methods: {
      drawHollowCircle() {
        let myChart = this.$echarts.init(document.getElementById('hollowCircle'))
        myChart.setOption({
          title: {text: ''},
          tooltip: {
            trigger: 'item',
            formatter: '{a}<br/>{b}:{c} ({d}%)'
          },
          legend: {
            orient: 'vertical',
            x: 'left',
            data: ['', '', '', '', '']
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: ['50%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'blod'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [
                {value: 335, name: ''},
                {value: 310, name: ''},
                {value: 234, name: ''},
                {value: 135, name: ''},
                {value: 1548, name: ''}
              ]
            }
          ]
        })
      },
      drawColumn() {
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('column'))
        // 
        myChart.setOption({
          title: {
            text: '',
            x: 'center',
            y: 'top',
            textStyle: {
              //
              color: '#ccc',
              //,'normal','italic','oblique'
              fontStyle: 'normal',
              // 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight: 'bold',
              //
              fontFamily: 'sans-serif',
              //
              fontSize: 18
            }
          },
          tooltip: {},
          xAxis: {
            data: ["", "", "", "", "", ""]
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }]
        })
      },
      drawCake() {
        let cake = this.$echarts.init(document.getElementById('cake'))
        // 
        cake.setOption({
          //copy
          backgroundColor: '#45515a',
          //
          title: {
            text: '',
            subtext: '',
            left: 'center',
            top: 20,
            textStyle: {
              color: '#ccc',
              fontStyle: 'italic'//
            }
          },
          //
          tooltip: {
            trigger: 'item',//
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          //
          legend: {
            orient: 'vertical',
            left: 'left',
            textStyle: {
              color: '#c8c8d0'
            },
            data: ['', '', '', '', '']  //name
          },
          //
          toolbox: {
            show: true,//
            feature: {
              dataView: {show: true}, //
              restore: {show: true},   //
              //dataZoom:{show:true}, //
              saveAsImage: {show: true},  //
              //magicType:{type:['line','bar']}//
            }
          },
          //
          visualMap: {
            show: false,
            min: 10,
            max: 650,
            dimension: 0, //[]1
            // seriesIndex: 4, //{}{}data
            inRange: {
              //inRange[xy]xyvalue
              color: ['red'],
              colorLightness: [0, 1],
              colorSaturation: [0, 1]
            }
          },
          //
          series: [
            {
              name: '',
              type: 'pie',
              radius: '55%',
              center: ['50%', '50%'],
              data: [
                {value: 335, name: ''},
                {value: 310, name: ''},
                {value: 274, name: ''},
                {value: 235, name: ''},
                {value: 400, name: ''}
              ].sort(function (a, b) {
                return a.value - b.value
              }),
              roseType: 'radius',//'area'
              label: { //
                normal: {  //normal
                  textStyle: {
                    color: 'rgba(255, 255, 255, 0.3)'
                  }
                }
              },
              labelLine: {  //
                normal: {
                  lineStyle: {
                    color: 'rgba(255, 255, 255, 0.3)'
                  },
                  smooth: 0.5, //0-1
                  length: 10,  //
                  length2: 20  //
                }
              },
              itemStyle: { //
                normal: {
                  color: '#97413c',
                  shadowBlur: 50,//
                  shadowColor: 'rgba(0, 0, 0, 0.5)'//
                }
              },

              animationType: 'scale', //scaleexpansion
              animationEasing: 'elasticOut', //
              animationDelay: function (idx) {  //idx
                return Math.random() * 200
              }
            }
          ]
        })
      },
      drawLines() {
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('lines'))
        // this.echarts1_option
        myChart.setOption({
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            y: 'bottom',
            x: 'center',
            data: ['A', 'B', 'C']
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['', '', '', '', '', '', '']
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: 'A',
              type: 'line',
              stack: '',
              data: [3, 54, 65, 43, 24, 5, 31]
            },
            {
              name: 'B',
              type: 'line',
              stack: '',
              data: [43, 5, 33, 23, 54, 53, 42]
            },
            {
              name: 'C',
              type: 'line',
              stack: '',
              data: [2, 45, 42, 23, 34, 3, 22]
            }
          ]
        })
      },
      drawLine() {
        let myChart = this.$echarts.init(document.getElementById('line'))
        myChart.setOption({
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            y: 'bottom',
            x: 'center',
            data: ['A']
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['', '', '', '', '', '', '']
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: 'A',
              type: 'line',
              stack: '',
              data: [3, 54, 65, 43, 24, 5, 31]
            }
          ]
        })
      },
      drawChartMap() {
        let myChart = this.$echarts.init(document.getElementById('chartMap'))
        // ,
        let myData = [
          {name: '1', value: [121.15, 31.89, 90]},
          {name: '2', value: [109.781327, 39.608266, 120]},
          {name: '3', value: [120.38, 37.35, 142]},
          {name: '4', value: [122.207216, 29.985295, 123]},
          {name: '5', value: [110.245672, 30.7787677, 566]}
        ]

        myChart.setOption({
          //  geo 
          geo: {
            map: 'china',//,world,beijingshanghai
            itemStyle: { // 
              normal: {       // 
                areaColor: '#6699CC',
                borderColor: '#fff',
              },
              emphasis: {         // 
                areaColor: '#e9fbf1'
              }
            }

          },
          // hover
          tooltip: {
            trigger: 'item',
            // tooltiptrigger'item''axis'
            //'item':
            //'axis':
            textStyle: {
              align: 'left'
            },
          },
          // 
          backgroundColor: '#404a59',
          // 
          color: 'red',
          // series
          series: [{
            name: '',//series
            type: 'scatter',//
            coordinateSystem: 'geo',// series
            data: myData,
            symbol: 'pin',
            symbolSize: [20, 20]
          }],

          // 
          visualMap: {
            type: 'continuous', // 
            min: 0,           // 
            max: 600,     // 
            calculable: true, // 
            inRange: {
              color: ['red']
              // 
            },
            textStyle: {
              color: '#fff' // 
            }
          }
        })
      }
    }
  }
</script>



