<style type="text/css">
  .coupon-details-form {
    width: 580px;
    height: 350px;
    margin: auto;
    text-align: left;
  }

  .coupon-details-form .el-form-item {
    width: 45%;
  }

  .coupon-details-form .el-form-item__label {
    width: 110px;
  }

  .info-div {
    width: 150px;
    text-align: left;
  }

  .coupon-details-form .el-form-item--small.el-form-item {
    margin-bottom: 0px;
  }
</style>
<template>
  <el-container>
    <el-main v-loading="loading" element-loading-text="" style="background-color: #FFFFFF;">
      <div style="width: 100%;float: left;clear: left;margin: auto;">
        <el-form :inline="true" :model="coupon" ref="coupon" class="demo-form-inline coupon-details-form">
          <el-form-item label="" prop="couponType">
            <div class="info-div" v-if="coupon.couponType === 1"></div>
            <div class="info-div" v-if="coupon.couponType === 2"></div>
            <div class="info-div" v-if="coupon.couponType === 3"></div>
          </el-form-item>
          <el-form-item label="" prop="gainType">
            <div class="info-div" v-if="coupon.gainType === 1"></div>
            <div class="info-div" v-if="coupon.gainType === 2"></div>
          </el-form-item>
          <br>
          <el-form-item label="" prop="couponName">
            <div class="info-div">{{coupon.couponName}}</div>
          </el-form-item>
          <el-form-item label="" prop="denomination">
            <div class="info-div">{{coupon.denomination}} </div>
          </el-form-item>
          <el-form-item label="" prop="gainAppletName">
            <div class="info-div">{{coupon.gainAppletName}}</div>
          </el-form-item>
          <el-form-item label="" prop="gainPrice">
            <div class="info-div"> {{coupon.gainPrice}}</div>
          </el-form-item>
          <el-form-item label="" prop="useAppletName">
            <div class="info-div">{{coupon.useAppletName}}</div>
          </el-form-item>
          <el-form-item label="" prop="usePrice">
            <div class="info-div"> {{coupon.usePrice}}</div>
          </el-form-item>
          <el-form-item label="" prop="makeIssueNum">
            <div class="info-div">{{coupon.makeIssueNum > 0 ? coupon.makeIssueNum + ' ' : ''}}</div>
          </el-form-item>
          <el-form-item label="" prop="alreadyIssueNum">
            <div class="info-div">{{coupon.alreadyIssueNum}} </div>
          </el-form-item>
          <el-form-item label="" prop="activityStart">
            <div class="info-div">{{coupon.activityStart}}</div>
          </el-form-item>
          <el-form-item label="" prop="activityOver">
            <div class="info-div">{{coupon.activityOver}}</div>
          </el-form-item>
          <el-form-item label="" prop="status">
            <div class="info-div">
              <el-link :underline="false" type="danger" v-if="coupon.status == -1"></el-link>
              <el-link :underline="false" target="_blank" v-if="coupon.status == 0"></el-link>
              <el-link :underline="false" type="success" v-if="coupon.status == 1"></el-link>
              <el-link :underline="false" type="danger" v-if="coupon.status == 2"></el-link>
              <el-link :underline="false" type="info" v-if="coupon.status == 3"></el-link>
            </div>
          </el-form-item>
          <el-form-item label="" prop="createTime">
            <div class="info-div">{{coupon.createTime}}</div>
          </el-form-item>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>
<script type="text/javascript">
    export default {
        name: 'coupon-details',
        data () {
            return {
                loading: false,
                coupon: {}
            }
        },
        created () {
            let id = this.$cookies.get('coupon_id')
            this.loadCouponInfo(id)
        },
        mounted () {

        },
        methods: {
            loadCouponInfo (id) {
                if (id) {
                    this.loading = true
                    this.$axios({
                        url: '/api/user/coupon/loadCouponDetails',
                        method: 'post',
                        data: {id: parseInt(id)}
                    }).then(res => {
                        console.info('', res.data)
                        if (res.data.code === '1') {
                            this.coupon = res.data.data
                        }
                        this.$cookies.remove('coupon_id')
                        this.$global.exitLoad(this, null, res.data)
                    }).catch(error => {
                        console.info('', error)
                        this.$global.exitLoad(this, null, '')
                    })
                }
            }
        }
    }
</script>



