<style type="text/css">
  .form-div {
    float: left;
    width: 350px;
  }

  .coupon-info-form {
    width: 100%;
    height: 620px;
    text-align: left;
  }

  .coupon-info-form .el-form-item__label {
    width: 110px;
  }

  .coupon-info-input {
    width: 230px;
  }

  .remark-div {
    float: right;
    width: 255px;
    color: #bfbfbf;
    margin-right: 20px;
  }

  .remark-div > .title {
    font-size: 14px;
    padding-top: 20px;
  }

  .remark-div > .content {
    width: 230px;
    line-height: 20px;
    margin-left: 20px;
    font-size: 12px;
    padding-top: 5px;
  }

  .remark-div > .tips {
    font-size: 12px;
    text-align: center;
    color: #3a8ee6;
    padding-top: 20px;
  }
</style>
<template>
  <el-container>
    <el-main v-loading="loading" element-loading-text="" style="background-color: #FFFFFF;text-align: left;">
      <div class="form-div">
        <el-form :inline="true" :model="coupon" :rules="rules" ref="coupon" class="coupon-info-form">
          <el-form-item label="" prop="couponType">
            <el-select v-model="coupon.couponType" placeholder=""
                       class="coupon-info-input" @change="chooseCouponType()">
              <el-option label="" value=''></el-option>
              <el-option label="" value='1'></el-option>
              <el-option label="" value='2'></el-option>
              <el-option label="" value='3'></el-option>
              <!--              <el-option label="" value='3'></el-option>-->
            </el-select>
          </el-form-item>
          <el-form-item label="" v-if="coupon.couponType">
            {{coupon.gainType === '1' ? '' : ''}}
          </el-form-item>
          <el-form-item label="" prop="couponName">
            <el-input v-model="coupon.couponName" maxLength="20" placeholder=""
                      class="coupon-info-input"></el-input>
          </el-form-item>
          <el-form-item label="" prop="denomination">
            <el-input v-model.number="coupon.denomination" maxlength="3" placeholder=""
                      class="coupon-info-input" @input="handleClick"></el-input>
          </el-form-item>
          <el-form-item label="" prop="gainAppletId" v-if="coupon.gainType !== '1'">
            <el-select v-model="coupon.gainAppletId" placeholder="" class="coupon-info-input">
              <el-option label="" value=''></el-option>
              <el-option v-for="(item, index) in appletList" :key="index" :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="gainPrice" v-if="coupon.gainType !== '1'">
            <el-input v-model.number="coupon.gainPrice" placeholder="10210" maxlength="5"
                      class="coupon-info-input" @input="handleClick"></el-input>
          </el-form-item>
          <el-form-item label="" prop="useAppletId">
            <el-select v-model="coupon.useAppletId" placeholder="" class="coupon-info-input">
              <el-option label="" value=''></el-option>
              <el-option v-for="(item, index) in appletList" :key="index" :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="usePrice">
            <el-input v-model.number="coupon.usePrice" placeholder=""
                      class="coupon-info-input" readonly="readonly" v-if="coupon.couponType === '1'"></el-input>
            <el-input v-model.number="coupon.usePrice" placeholder="" maxlength="5"
                      class="coupon-info-input" @input="handleClick" v-else></el-input>
          </el-form-item>
          <el-form-item label="" prop="makeIssueNum">
            <el-input v-model.number="coupon.makeIssueNum" maxlength="5" placeholder=""
                      class="coupon-info-input" @input="handleClick"></el-input>
            <br>
            <!--            <div class="remark">0()</div>-->
          </el-form-item>
          <el-form-item label="" prop="activityStart">
            <el-date-picker type="datetime" placeholder="" v-model="coupon.activityStart"
                            :format="format" :value-format="valueFormat" :picker-options="pickerOptionsStart"
                            style="width: 230px;"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="activityOver">
            <el-date-picker type="datetime" placeholder="" v-model="coupon.activityOver"
                            :format="format" :value-format="valueFormat" :picker-options="pickerOptionsStart"
                            style="width: 230px;"></el-date-picker>
          </el-form-item>
          <el-form-item label=" ">
            <el-button type="success" @click="onSubmit()" class="coupon-info-input" style="text-align: center;">
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="remark-div">
        <div class="title"></div>
        <div class="content">
          <label style="font-weight: bold;"></label>
          
        </div>
        <div class="content">
          <label style="font-weight: bold;"></label>
          
        </div>
        <div class="content">
          <label style="font-weight: bold;"></label>
          
        </div>
        <div class="content">
          
        </div>
        <div class="title"></div>
        <div class="content">0()</div>
        <div class="tips"></div>
      </div>
    </el-main>
  </el-container>
</template>
<script type="text/javascript">
    import {Loading} from 'element-ui'

    export default {
        name: 'coupon-create',
        data () {
            return {
                loading: false,
                format: 'yyyyMMdd',
                valueFormat: 'yyyy/MM/dd',
                appletList: [],
                coupon: {
                    couponType: '',
                    couponName: '',
                    denomination: '',
                    gainType: '',
                    gainAppletId: '',
                    gainPrice: '',
                    useAppletId: '',
                    usePrice: '',
                    makeIssueNum: '',
                    activityStart: '',
                    activityOver: ''
                },
                rules: {
                    couponType: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    couponName: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'string', min: 1, max: 20, message: '1-20', trigger: 'blur'}
                    ],
                    denomination: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 1, max: 99999, message: '1-999', trigger: 'blur'}
                    ],
                    gainAppletId: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gainPrice: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '1-99999', trigger: 'blur'}
                    ],
                    useAppletId: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    usePrice: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '0-99999', trigger: 'blur'}
                    ],
                    makeIssueNum: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '0-99999', trigger: 'blur'}
                    ],
                    activityStart: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    activityOver: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                pickerOptionsStart: {
                    disabledDate (time) {
                        return time.getTime() < Date.now() - 8.64e7
                    }
                }
            }
        },
        created () {
            this.loadAddCreateCoupon()
        },
        mounted () {

        },
        methods: {
            loadAddCreateCoupon () {
                this.loading = true
                this.$axios({
                    url: '/api/user/coupon/loadAddCouponInfo',
                    method: 'post'
                }).then(res => {
                    if (res.data.code === '1') {
                        this.appletList = res.data.data
                    }
                    this.$global.exitLoad(this, null, res.data)
                }).catch(error => {
                    console.info('', error)
                    this.$global.exitLoad(this, null, '')
                })
            },
            onSubmit () {
                this.$refs['coupon'].validate((valid) => {
                    if (valid) {
                        let loading = Loading.service({fullscreen: true, text: ''})
                        this.$axios({
                            url: '/api/user/coupon/addCouponInfo',
                            method: 'post',
                            data: this.coupon
                        }).then(res => {
                            console.info('', res.data)
                            let that = this
                            res.data.code === '1' ? this.$message.success({
                                message: res.data.data,
                                duration: 1000,
                                onClose: function () {
                                    that.$emit('refreshList')
                                }
                            }) : this.$message.error(res.data.data)
                            this.$global.exitLoad(this, loading, res.data)
                        }).catch(error => {
                            console.info('', error)
                            this.$global.exitLoad(this, loading, '')
                        })
                    }
                })
            },
            chooseCouponType () {
                let couponType = this.coupon.couponType
                switch (couponType) {
                    case '1':
                        this.coupon.gainType = '1'
                        this.coupon.gainAppletId = '0'
                        this.coupon.gainPrice = 0
                        this.coupon.usePrice = 0
                        break
                    case '2':
                        this.coupon.gainType = '1'
                        this.coupon.gainAppletId = '0'
                        this.coupon.usePrice = ''
                        break
                    case '3':
                        this.coupon.gainType = '2'
                        this.coupon.gainAppletId = ''
                        this.coupon.gainPrice = ''
                        this.coupon.usePrice = ''
                        break
                    case '4':
                        this.coupon.gainType = '2'
                        this.coupon.gainAppletId = ''
                        this.coupon.gainPrice = ''
                        this.coupon.usePrice = ''
                        break
                    default:
                        this.coupon.gainType = ''
                        this.coupon.gainAppletId = ''
                        this.coupon.gainPrice = ''
                        this.coupon.usePrice = ''
                        break
                }
            },
            handleClick () {
                try {
                    this.coupon.denomination = this.coupon.denomination.replace(/[^\d]/g, '')
                } catch (e) {
                }
                try {
                    this.coupon.gainPrice = this.coupon.gainPrice.replace(/[^\d]/g, '')
                } catch (e) {
                }
                try {
                    this.coupon.usePrice = this.coupon.usePrice.replace(/[^\d]/g, '')
                } catch (e) {
                }
                try {
                    this.coupon.makeIssueNum = this.coupon.makeIssueNum.replace(/[^\d]/g, '')
                } catch (e) {
                }
            }
        }
    }
</script>



